import React, { useState, useCallback } from "react";
import './App.css';
import pic from './111.png'

import { Input, Select, message, Progress, Table, Button } from 'antd';

const { Search } = Input;
const { Option } = Select;

let timer = null

const App = () =>  {
  const [loading, setLoading] = useState(false)
  const [times, setTimes] = useState(1);
  const [show, setShow] = useState(false);
  const [percent, setPercent] = useState(0);
  const [tableData, setTableData] = useState([]);
  const conicColors = { '0%': '#87d068', '50%': '#ffe58f', '100%': '#ffccc7' };

  const sendAjax = (url, runs) => {
    console.log("-> url, runs", url, runs);
    fetch(`http://localhost:80/analyze`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        url: url,
        runs: runs
      }) // JSON
    })
      .then(response => response.json())
      .then(data => {
        console.log(data)
        if (data.success) {
          const td = data.data.result.map((e, i) => {
            return {
              times: i + 1,
              performance: `${e.performance}`,
              link: e.link
            }
          })
          setTableData(td)
          clearInterval(timer)
          setLoading(false)
          timer = null
          setPercent(100)
        }
      })
      .catch(error => console.error('Error:', error));
  }

  const fakeProgress = () => {
    setShow(true)
    const time = 100 / times
    let t = 0
    if (t >= times - 1) {
      clearInterval(timer)
      timer = null
      setPercent(90)
      return
    } else {
      t += 1
      setPercent(t * time)
    }
    timer = setInterval(() => {
      if (t > times - 1) {
        clearInterval(timer)
        timer = null
        setPercent(90)
      } else {
        t += 1
        setPercent(t * time)
      }
    }, 10000)
  }

  const onSearch = useCallback((v) => {
    if (v) {
      const regex = /^(https?:\/\/)?([\da-z.-]+)\.([a-z.]{2,6})([\/\w .-]*)*\/?$/;
      if (regex.test(v)) {
        setTableData([])
        setPercent(0)
        setShow(false)
        message.success('');
        setLoading(true)
        fakeProgress()
        sendAjax(v, times)
        return
      }
      message.error('url');
      return
    }
    message.error('url');
  }, [times])

  const onChange = (v) => {
    switch (v) {
      case ' 1 ':
        setTimes(1);
        break
      case ' 5 ':
        setTimes(5);
        break
      case ' 10 ':
        setTimes(10);
        break
      case ' 20 ':
        setTimes(20);
        break
      default:
        setTimes(1)
    }
  }

  const selectBefore = (
    <Select defaultValue=" 1 " onChange={onChange}>
      <Option value=" 1 "> 1 </Option>
      <Option value=" 5 "> 5 </Option>
      <Option value=" 10 "> 10 </Option>
      <Option value=" 20 "> 20 </Option>
    </Select>
  );

  return (
    <div className="App">
      <div className="Search">
        <div className="Pic">
          <img src={pic} alt="pic" style={{width: '250px'}}/>
        </div>
        <Search
          placeholder="Url"
          enterButton={loading ? "Testing" : "Test"}
          size="large"
          loading={loading}
          addonBefore={selectBefore}
          onSearch={onSearch}
        />
        {
          show && (
            <div className="Result">
              {
                tableData.length < 1 ? (
                  <>
                    <p></p>
                    <Progress percent={percent} strokeColor={conicColors} />
                  </>
                  ) : (
                    <Table columns={
                      [
                        {
                          title: '',
                          dataIndex: 'times',
                          key: 'times'
                        },
                        {
                          title: '',
                          dataIndex: 'performance',
                          key: 'performance'
                        },
                        {
                          title: '',
                          dataIndex: 'link',
                          key: 'link',
                          render: (v) => {
                            return (
                              <Button type="link" href={`//localhost:80/download/${v}`}>{v}</Button>
                            )
                          }
                        }
                      ]
                    } dataSource={tableData} />
                  )
              }
            </div>
          )
        }
      </div>
    </div>
  );
}

export default App;
